import { Injectable } from '@angular/core';

declare var chrome: any;

@Injectable()
export class ContextMenuService {

  private menuItems: Array<{}> = new Array<{}>();
  constructor() { }

  addContextMenu(): void {
    this.readMenu();
    this.menuItems.forEach(item => {
      let id = chrome.contextMenus.create(item);
    });
  }
  private readMenu(): void {
    this.menuItems.push({
      'contexts': ["selection"],
      'title': 'Add selection to journey',
      'onclick': this.sendMessage
    });
  }
  sendMessage(info: any, tab: any): void {
    chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
      console.log(tabs);
      chrome.tabs.sendMessage(tabs[0].id, { info: info, tab: tab });
    });
  }

}



